export default {
	props: {
		rate: {
			type: Number,
			default: 0,
		},
		starsize: {
			type: Number,
			default: 20
		},
		fontsize: {
			type: Number,
			default: 20
		},
		fontcolor: {
			type: String,
			default: "#ccc"
		}
	},
	data() {
		return {
			light: 0,
			half: 0,
			gray: 0,
			ratetext: 0
		};
	},
	created() {
		this.attached()
	},
	methods: {
		attached() {
			var rate = this.rate
			var intRate = parseInt(rate)
			var light = parseInt(intRate/2) //满星
			var half = intRate%2 // 半星
			var gray = 5 - light - half // 灰星
			// 格式化评分
			var ratetext = rate && rate>0 ? rate.toFixed(1) : '未评分'
			this.light = light
			this.half = half
			this.gray = gray
			this.ratetext = ratetext
		
		}
	}
}
